import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { getrekeningList } from "../../actions/rekeningAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class AccordGiroMurni extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBank: [],
    };
  }
  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("GiroNetto", this.props.GiroNetto);
  }
  getlocal() {
    var acc_to = "",
      bank_to = "",
      bank_giro = "",
      giro_netto = "",
      giro_rp = "",
      harga_emas = "",
      no_giro = "",
      tanggal_giro = "";
    var local = JSON.parse(localStorage.getItem("alatBayarGiroRp"))
      ? JSON.parse(localStorage.getItem("alatBayarGiroRp"))
      : "kosong";
    if (local === "kosong") {
      acc_to = "";
      bank_to = "";
      bank_giro = "";
      giro_netto = 0;
      giro_rp = 0;
      harga_emas = 0;
      no_giro = "";
      tanggal_giro = "";
    } else {
      acc_to = local[0].acc_to;
      bank_to = local[0].bank_to;
      bank_giro = local[0].bank_giro;
      giro_netto = local[0].giro_netto;
      giro_rp = local[0].giro_rp;
      harga_emas = local[0].harga_emas;
      no_giro = local[0].no_giro;
      tanggal_giro = local[0].tanggal_giro;
    }
    this.props.change("giro", giro_rp);
    this.props.change("GiroNetto", giro_netto);
    this.props.change("tujuan", `${acc_to}||${bank_to}`);
    this.props.change("bank_giro", bank_giro);
    this.props.change("harga", harga_emas);
    this.props.change("no_giro", no_giro);
    this.props.change("tanggal", tanggal_giro);
  }

  componentDidMount() {
    this.props.dispatch(getrekeningList());
    this.getlocal();
  }
  render() {
    return (
      <div className="col-lg-6 mt-3">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-8 col-md-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseGiro"
                      aria-expanded="true"
                      aria-controls="collapseTransfer"
                    >
                      Giro
                      <span className="float-right">
                        <i className="mdi mdi-chevron-down accordion-arrow" />
                      </span>
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="GiroNetto"
                    type="telp"
                    component="input"
                    className="form-control"
                    readOnly={true}
                    {...currencyMask}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseGiro"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">giro</label>
                      <Field
                        name="giro"
                        component="input"
                        type="tel"
                        className="form-control"
                        onBlur={this.onChange()}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Harga </label>
                      <div className="form-gorup">
                        <Field
                          name="harga"
                          component="input"
                          type="tel"
                          className="form-control"
                          onBlur={this.onChange()}
                          readOnly
                          {...currencyMask}
                        />
                      </div>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="GiroNetto"
                        label="Netto"
                        type="telp"
                        component={renderField}
                        readOnly={true}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <Field
                        name="no_giro"
                        label="No Giro"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <Field
                        name="bank_giro"
                        label="Bank Giro"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <label htmlFor="">A/C Tujuan</label>
                      <Field
                        name="tujuan"
                        className="form-control"
                        component="select"
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          silahkan pilih
                        </option>
                        {this.props.rekening.map((list) => (
                          <option
                            value={`${list.no_rekening}||${list.nama_bank}`}
                            key={list.no_rekening}
                          >
                            {list.atas_nama}
                          </option>
                        ))}
                      </Field>
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <Field
                        name="tanggal"
                        label="Tanggal"
                        type="date"
                        component={renderField}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary" type="submit">
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          {/* end card*/}
        </div>
      </div>
    );
  }
}

AccordGiroMurni = reduxForm({
  form: "AccordGiroMurni",
  enableReinitialize: true,
})(AccordGiroMurni);
const selector = formValueSelector("AccordGiroMurni"); // <-- same as form name
export default connect((state) => {
  return {
    GiroNetto: parseFloat(selector(state, "giro")),
    rekening: state.provinsi.rekening,
  };
})(AccordGiroMurni);
